<template>
  <div class="app-page">
    <page-content class="page-content">
      <van-pull-refresh v-model="isLoadingOnRefresh" @refresh="onRefresh">
        <van-list v-model="scrolLoading" :finished="scrolFinished" @load="onLoad" :immediate-check="false" finished-text="没有更多了">
          <div class="list" v-for="(item, index) of list" :key="index">
            <div class="list-flex">
              <span class="icon"></span>
              <div class="message-type">{{item.title}}</div>
            </div>
            <div class="message-time">{{item.price}}</div>
            <div class="message-info">{{item.w_price}}</div>
          </div>
        </van-list>
      </van-pull-refresh>
    </page-content>
  </div>
</template>

<script>
import PageContent from '@/components/page/content'
export default {
  name: 'messagecenter',
  components: {
    PageContent
  },
  data () {
    return {
      list: [
        {title: '通知', price: '2018-12-24', w_price: '欢迎您加入农艺微店，足不出门，在线订菜欢迎您加入农艺微店'},
        {title: '标题222标题标题标题标题', price: 256.23, w_price: 89.36},
        {title: '标题333标题标题标题标题', price: 356.23, w_price: 89.36},
        {title: '标题444标题标题标题标题', price: 456.23, w_price: 89.36},
        {title: '标题555标题标题标题标题', price: 556.23, w_price: 89.36},
        {title: '标题666标题标题标题标题', price: 656.23, w_price: 89.36}
      ],
      isLoadingOnRefresh: false,
      // 滚动刷新变量
      scrolLoading: false,
      scrolFinished: false
    }
  },
  methods: {
    onRefresh () {
      setTimeout(() => {
        this.$toast('刷新成功')
        this.isLoadingOnRefresh = false
        this.count++
      }, 500)
    },
    onLoad () {
      console.log('1')
    }
  }
}
</script>

<style lang="scss" scoped>
  @import "@/styles/variables.scss";
  .app-page {}
  .page-content{
    background-color: $whitee6e6e6;
    padding: 20px!important;
    .van-pull-refresh {
      height: 100%;
      overflow: auto;
    }
    .list {
      padding: 30px 32px;
      /*height: 180px;*/
      line-height: 35px;
      background-color: $whiteffffff;
      display: flex;
      align-items: flex-start;
      flex-direction: column;
      padding-left: 20px;
      margin-bottom: 20px;
      font-family: $font-family-default;
      border-radius: 10px;
    }
    .list-flex{
      height: 30%;
      display: flex;
      align-items: center;
      .icon{
        width: 13px;
        height: 13px;
        border-radius: 50%;
        background-color: $redff0000;
      }
      .message-type{
        font-size: 24px;
        margin-left: 10px;
      }
    }
    .message-time{
      height: 20%;
      font-size: 20px;
      color: gray;
      margin: 10px 0;
    }
    .message-info{
      flex: 1;
      font-size: 28px;
      overflow: hidden;
      text-overflow:ellipsis;
    }
  }
</style>



